/* eslint-disable object-shorthand */
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LocalService } from '../../services/local.service';
import { RestService } from '../../services/rest.service';
import { UtilityService } from '../../services/utility.service';
import { EventsService } from '../../services/events.service';

@Component({
  selector: 'app-bookings',
  templateUrl: './bookings.page.html',
  styleUrls: ['./bookings.page.scss'],
})
export class BookingsPage implements OnInit {
  loggeduser: any;
  segment: any = 'current';

  bookings: any = [];
  pendingBookings: any = [];
  currency: any;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router
  ) {
    this.loggeduser = this.localService.getUser();
    this.currency = this.localService.getSettings().currency_symbol;
    this.utilityService.loadingOpen('Loading...');
    this.restService
      .getData('user/' + this.loggeduser.id + '/bookings')
      .subscribe(
        (data) => {
          this.bookings = data;
          this.restService
            .getData('user/' + this.loggeduser.id + '/pendingbookings')
            .subscribe(
              (success) => {
                this.pendingBookings = success;
                this.utilityService.loadingClose();
              },
              (err) => {
                // handle error
              }
            );
        },
        (err) => {
          // handle error
        }
      );
  }

  ngOnInit() {}

  bookingDetails(id) {
    this.router.navigate(['/orderdetails'], {
      queryParams: {
        id: id,
      },
    });
  }

  doRefresh(event) {
    this.restService
      .getData('user/' + this.loggeduser.id + '/bookings')
      .subscribe(
        (data) => {
          this.bookings = data;
          this.restService
            .getData('user/' + this.loggeduser.id + '/pendingbookings')
            .subscribe(
              (success) => {
                this.pendingBookings = success;
                event.target.complete();
              },
              (err) => {
                // handle error
                event.target.complete();
              }
            );
        },
        (err) => {
          // handle error
          event.target.complete();
        }
      );
  }
}
